@keyframes row-1 {
    100% { bottom: 0; }
}
#view-project-1 {
    bottom: -2000px;
    animation: row-1 300ms forwards;
    animation-delay: .7s;
}
#view-project-2 {
    bottom: -2000px;
    animation: row-1 600ms forwards;
    animation-delay: .7s;
}
@keyframes fade-in {
    100% { opacity: 1; }
}
.preview-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin: 50px auto;
    max-width: 2000px;
}
.project-view {
    position: relative;
    background: #001624;
    height: 530px;
}
.project-title {
    position: absolute;
    z-index: 10;
    width: 100%;
    font-family: 'Raleway', sans-serif;
    color: #32adff;
    top: -25px;
    font-size: 2.4em;
    opacity: 0;
}
.project-text {
    position: absolute;
    z-index: 10;
    width: 100%;
    padding: 15px 0;
    font-family: 'Raleway', sans-serif;
    color: #fff;
    font-size: 1.6em;
    top: -10px;
    opacity: 0;
}
.project-description {
    position: absolute;
    top: 40%;
    font-family: 'Raleway', sans-serif;
    color: #fff;
    font-size: 1.2em;
    margin: 0 5px;
    opacity: 0;
}
.project-view img {
    width: 600px;
    transition: .5s;
}
.project-link {
    display: flex;
    justify-content: center;
    text-decoration: none;
}
.project-view button {
    position: absolute;
    cursor: pointer;
    padding: 15px;
    border: 5px solid #1F618D;
    font-family: 'Raleway', sans-serif;
    font-weight: 900;
    font-size: 1.5em;
    z-index: 10;
    width: 200px;
    bottom: 15px;
    color: black;
    opacity: 0;
    transition: .5s;
}
.project-view:hover img {
    opacity: 0;
    transition: .5s;
}
.project-view:hover h2 {
    top: 30px;
    opacity: 1;
    transition: .5s;
}
.project-view:hover .project-text {
    opacity: 1;
    top: 110px;
    transition: .5s;
}
.project-view:hover .project-description {
    opacity: 1;
    transition: 1s;
    transition-delay: .5s;
}
.project-view:hover button {
    opacity: 1;
    bottom: 100px;
    transition: .5s;
}
.project-view button:hover {
    background-color: #1F618D;
    color: #fff;
}

@keyframes fall-in {
    0% {transform: rotate(15deg);}
    95% {transform: rotate(-5deg);}
    100% {transform: rotate(0deg);}
}
.portfolio-nav-close #portfolio-about {
    position: absolute;
    font-family: 'Raleway', sans-serif;
    color: #fff;
    font-size: 1.6em;
    top: 65px;
    right: -1000px;
    transition: 1s;
}
.portfolio-nav-open #portfolio-about {
    position: absolute;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
    color: #fff;
    font-size: 1.6em;
    top: 65px;
    right: 300px;
    animation: fall-in 1s forwards;
    transition: 1s;
}
.portfolio-nav-close #portfolio-contact {
    position: absolute;
    font-family: 'Raleway', sans-serif;
    color: #fff;
    font-size: 1em;
    top: 65px;
    right: -1000px;
    transition: 1s;
}
.portfolio-nav-open #portfolio-contact {
    position: absolute;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
    color: #fff;
    font-size: 1.6em;
    top: 65px;
    right: 160px;
    animation: fall-in 1.5s forwards;
    transition: 1.5s;
}
#portfolio-about:hover, #portfolio-contact:hover {
    color: #32adff;
    transition: .5s;
}
@media(max-width: 800px) {
    @keyframes fade-in {
        100% { opacity: 1; }
    }
    .preview-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }
    .project-view {
        position: relative;
        background: #001624;
        height: 305px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
    }
    .project-view img {
        width: 300px;
    }
    .project-title {
        font-size: 1.8em;
    }
    .project-text {
        font-size: 1.2em;
    }
    .project-description {
        font-size: 1em;
    }
    .project-view button {
        padding: 10px;
        font-size: 1em;
        width: 100px;
    }
    .project-view:hover img {
        opacity: 0;
        transition: .5s;
    }
    .project-view:hover h2 {
        top: 30px;
        opacity: 1;
        transition: .5s;
    }
    .project-view:hover .project-text {
        opacity: 1;
        top: 50px;
        transition: .5s;
    }
    .project-view:hover .project-description {
        opacity: 1;
        top: 120px;
        transition: 1s;
        transition-delay: .5s;
    }
    .project-view:hover button {
        opacity: 1;
        bottom: 30px;
        transition: .5s;
    }
    @keyframes fall-in {
        0% {transform: rotate(15deg);}
        95% {transform: rotate(-5deg);}
        100% {transform: rotate(0deg);}
    }
    .portfolio-nav-close #portfolio-about {
        position: absolute;
        font-family: 'Raleway', sans-serif;
        color: #fff;
        font-size: 1.6em;
        top: 65px;
        right: -1000px;
        transition: 1s;
    }
    .portfolio-nav-open #portfolio-about {
        position: absolute;
        cursor: pointer;
        font-family: 'Raleway', sans-serif;
        color: #fff;
        font-size: 1.6em;
        top: 65px;
        right: 300px;
        animation: fall-in 1s forwards;
        transition: 1s;
    }
    .portfolio-nav-close #portfolio-contact {
        position: absolute;
        font-family: 'Raleway', sans-serif;
        color: #fff;
        font-size: 1em;
        top: 65px;
        right: -1000px;
        transition: 1s;
    }
    .portfolio-nav-open #portfolio-contact {
        position: absolute;
        cursor: pointer;
        font-family: 'Raleway', sans-serif;
        color: #fff;
        font-size: 1.6em;
        top: 65px;
        right: 160px;
        animation: fall-in 1.5s forwards;
        transition: 1.5s;
    }
    #portfolio-about:hover, #portfolio-contact:hover {
        color: #32adff;
        transition: .5s;
    }
}